import { useEffect, useState } from 'react'
import axios from 'axios'
import { motion } from 'framer-motion'
import LeftSidebar from '../components/LeftSidebar'
import TopHeaderBar from '../components/TopHeaderBar'
import TaskBoard from '../components/TaskBoard'
import AIPanel from '../components/AIPanel'

const fadeInUp = {
  initial: { opacity: 0, y: 20 },
  animate: { opacity: 1, y: 0 },
  transition: { duration: 0.5, ease: [0.22, 1, 0.36, 1] }
}

const staggerContainer = {
  animate: {
    transition: {
      staggerChildren: 0.1
    }
  }
}

export default function Dashboard() {
  const [me, setMe] = useState<any>(null)
  const [statsData, setStatsData] = useState({
    activeTasks: 0,
    overdueTasks: 0,
    projects: 0,
    completedToday: 0,
    teamMembers: 0,
    productivityScore: 0
  })
  const [recentActivity, setRecentActivity] = useState<any[]>([])
  const [aiSuggestion, setAiSuggestion] = useState('')

  useEffect(() => {
    const t = localStorage.getItem('token')
    if (!t) return

    const base = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:4000'
    const headers = { Authorization: `Bearer ${t}` }

    // Get current user
    axios.get(`${base}/api/auth/me`, { headers })
      .then(r => setMe(r.data))
      .catch(() => {})

    // Get stats
    Promise.all([
      axios.get(`${base}/api/tasks`, { headers }),
      axios.get(`${base}/api/projects`, { headers }),
      axios.get(`${base}/api/users`, { headers }),
    ])
      .then(([tasksRes, projectsRes, usersRes]) => {
        const tasks = Array.isArray(tasksRes.data) ? tasksRes.data : []
        const projects = Array.isArray(projectsRes.data) ? projectsRes.data : []
        const users = Array.isArray(usersRes.data) ? usersRes.data : []

        const activeTasks = tasks.filter((t: any) => (t?.status || 'todo') !== 'done').length
        const overdueTasks = tasks.filter((t: any) => {
          if (!t?.dueDate) return false
          return new Date(t.dueDate) < new Date() && t.status !== 'done'
        }).length
        const completedToday = tasks.filter((t: any) => {
          if (t?.status !== 'done' || !t?.updatedAt) return false
          const updated = new Date(t.updatedAt)
          const today = new Date()
          return updated.toDateString() === today.toDateString()
        }).length
        const teamMembers = users.filter((u: any) =>
          ['ADMIN', 'MANAGER', 'STAFF'].includes(String(u?.role || ''))
        ).length

        // Calculate productivity score (example algorithm)
        const totalTasks = tasks.length || 1
        const doneRatio = (totalTasks - activeTasks) / totalTasks
        const productivityScore = Math.round(doneRatio * 100)

        setStatsData({
          activeTasks,
          overdueTasks,
          projects: projects.length,
          completedToday,
          teamMembers,
          productivityScore
        })
      })
      .catch(() => {
        setStatsData({
          activeTasks: 0,
          overdueTasks: 0,
          projects: 0,
          completedToday: 0,
          teamMembers: 0,
          productivityScore: 0
        })
      })
  }, [])

  const stats = [
    { label: 'Active Tasks', value: String(statsData.activeTasks ?? 0), icon: (
      <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5H7a2 2 0 00-2 2v12a2 2 0 002 2h10a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2m-6 9l2 2 4-4" />
      </svg>
    )},
    { label: 'Projects', value: String(statsData.projects ?? 0), icon: (
      <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 7v10a2 2 0 002 2h14a2 2 0 002-2V9a2 2 0 00-2-2h-6l-2-2H5a2 2 0 00-2 2z" />
      </svg>
    )},
    { label: 'Team Members', value: String(statsData.teamMembers ?? 0), icon: (
      <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4.354a4 4 0 110 5.292M15 21H3v-1a6 6 0 0112 0v1zm0 0h6v-1a6 6 0 00-9-5.197M13 7a4 4 0 11-8 0 4 4 0 018 0z" />
      </svg>
    )},
  ]

  return (
    <div className="min-h-screen bg-[#0a0a0f]">
      <NavBar />
      
      <main className="max-w-7xl mx-auto p-6">
        <motion.div 
          className="grid grid-cols-1 lg:grid-cols-12 gap-6"
          variants={staggerContainer}
          initial="initial"
          animate="animate"
        >
          {/* Main Content */}
          <div className="lg:col-span-8 xl:col-span-9">
            {/* Header */}
            <motion.div variants={fadeInUp} className="mb-8">
              <div className="flex items-center justify-between">
                <div>
                  <h1 className="text-3xl font-light text-white">
                    Welcome back, <span className="font-semibold text-amber-400">{me?.name || 'Guest'}</span>
                  </h1>
                  <p className="text-sm text-white/40 mt-1">{me?.role || 'Guest'} • KingdomX</p>
                </div>
                <motion.button
                  whileHover={{ scale: 1.02 }}
                  whileTap={{ scale: 0.98 }}
                  className="px-5 py-2.5 text-sm font-medium text-[#0a0a0f] bg-gradient-to-r from-amber-300 to-amber-500 rounded-lg transition-all duration-300 hover:shadow-[0_0_20px_-5px_rgba(251,191,36,0.5)]"
                >
                  + New Project
                </motion.button>
              </div>
            </motion.div>

            {/* Stats Grid */}
            <motion.section variants={fadeInUp}>
              <div className="grid grid-cols-1 sm:grid-cols-3 gap-4">
                {stats.map((stat, index) => (
                  <motion.div
                    key={stat.label}
                    whileHover={{ y: -4, transition: { duration: 0.2 } }}
                    className="p-5 bg-white/5 backdrop-blur-sm border border-white/10 rounded-xl hover:border-amber-500/30 transition-all duration-300"
                  >
                    <div className="flex items-center justify-between">
                      <div className="p-2 bg-amber-500/10 rounded-lg">
                        <span className="text-amber-400">{stat.icon}</span>
                      </div>
                    </div>
                    <div className="mt-4">
                      <div className="text-2xl font-semibold text-white">{stat.value}</div>
                      <div className="text-sm text-white/50">{stat.label}</div>
                    </div>
                  </motion.div>
                ))}
              </div>
            </motion.section>

            {/* Task Board */}
            <motion.section variants={fadeInUp} className="mt-8">
              <div className="flex items-center justify-between mb-4">
                <h2 className="text-xl font-medium text-white">Your Tasks</h2>
                <button className="text-sm text-amber-400 hover:text-amber-300 transition-colors">
                  View All
                </button>
              </div>
              <div className="bg-white/5 backdrop-blur-sm border border-white/10 rounded-xl p-4">
                <TaskBoard />
              </div>
            </motion.section>
          </div>

          {/* Sidebar */}
          <motion.aside variants={fadeInUp} className="lg:col-span-4 xl:col-span-3 space-y-4">
            <div className="bg-white/5 backdrop-blur-sm border border-white/10 rounded-xl p-5">
              <AIPanel />
            </div>
            
            <div className="bg-white/5 backdrop-blur-sm border border-white/10 rounded-xl p-5">
              <h4 className="font-medium text-white mb-4">Quick Actions</h4>
              <div className="space-y-3">
                <motion.button 
                  whileHover={{ scale: 1.02 }}
                  whileTap={{ scale: 0.98 }}
                  className="w-full px-4 py-3 text-sm font-medium text-[#0a0a0f] bg-gradient-to-r from-amber-300 to-amber-500 rounded-lg transition-all duration-300 hover:shadow-[0_0_15px_-5px_rgba(251,191,36,0.5)]"
                >
                  Create Project
                </motion.button>
                <motion.button 
                  whileHover={{ scale: 1.02 }}
                  whileTap={{ scale: 0.98 }}
                  className="w-full px-4 py-3 text-sm font-medium text-white/80 border border-white/20 rounded-lg hover:border-amber-500/50 hover:text-amber-400 transition-all duration-300"
                >
                  Invite Team
                </motion.button>
              </div>
            </div>

            {/* Recent Activity */}
            <div className="bg-white/5 backdrop-blur-sm border border-white/10 rounded-xl p-5">
              <h4 className="font-medium text-white mb-4">Recent Activity</h4>
              {recentActivity.length ? (
                <div className="space-y-4">
                  {recentActivity.map((item, i) => (
                    <div key={i} className="flex items-start gap-3">
                      <div className="w-2 h-2 rounded-full bg-amber-400 mt-1.5 shrink-0" />
                      <div>
                        <p className="text-sm text-white/70">{item?.text}</p>
                        <p className="text-xs text-white/40 mt-0.5">{item?.time}</p>
                      </div>
                    </div>
                  ))}
                </div>
              ) : null}
            </div>
          </motion.aside>
        </motion.div>
      </main>
    </div>
  )
}